:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-middle: hsl(5, 77%, 71%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-light: hsl(230, 22%, 74%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle-light: hsl(230, 19%, 60%);
  --toggle-button-light: hsl(228, 46%, 96%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-card-hover: hsl(228, 25%, 27%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-card-hover: hsl(228, 33%, 91%);
  --light-text1: hsl(230, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
  --background: var(--light-bg);
  --text-color: var(--light-text2);
  --text-color2: var(--light-text1);
  --card-bg: var(--light-card);
  --card-hover: var(--light-card-hover);
  --toggle: var(--toggle-light);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-button: var(--toggle-button-light);
}

body.light {
  --background: var(--light-bg);
  --text-color: var(--light-text2);
  --text-color2: var(--light-text1);
  --card-bg: var(--light-card);
  --card-hover: var(--light-card-hover);
  --toggle: var(--toggle-light);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-button: var(--toggle-button-light);
}

body.dark {
  --background: var(--dark-bg);
  --text-color: var(--dark-text2);
  --text-color2: var(--dark-text1);
  --card-bg: var(--dark-card);
  --card-hover: var(--dark-card-hover);
  --toggle: var(--light-bg);
  --toggle-bg: linear-gradient(
    225deg,
    var(--toggle-bg-end) 0%,
    var(--toggle-bg-start) 98.02%
  );
  --toggle-button: var(--dark-bg);
}

:root {
  --font-inter: "Inter", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  background: var(--background);
  color: var(--text-color);
  font-family: var(--font-inter);
  min-width: 20rem;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 69.375rem;
  margin: 0 auto;
  padding: 0 0.9375rem;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.625rem;
}
@media (min-width: 1024px) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.header {
  padding: 1.25rem 0 1.875rem;
  display: flex;
  flex-wrap: wrap;
}
@media (min-width: 768px) {
  .header {
    align-items: center;
    justify-content: space-between;
  }
}
.header__title {
  width: 100%;
}
@media (min-width: 768px) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  display: block;
  font-size: 0.875rem;
  line-height: 1.28;
  font-weight: 700;
  color: var(--text-color2);
}

.toggle {
  display: grid;
  grid-template-columns: auto 2.75rem;
  border: 0;
  grid-gap: 0.1875rem 0.5rem;
  padding: 0;
  margin: 0 0 0 auto;
}
@media (min-width: 768px) {
  .toggle {
    margin-left: 0;
  }
}
.toggle__mode {
  font-size: 0.875rem;
  grid-column: 1/2;
  align-self: center;
  color: var(--toggle);
  font-weight: 700;
}
.toggle__wrapper {
  height: 1.5rem;
  grid-column: 2/3;
  position: relative;
  overflow: hidden;
}
.toggle__wrapper input[type=radio] {
  opacity: 0;
  margin: 0 -0.125rem;
  width: 50%;
  height: 1.5rem;
  cursor: pointer;
}
.toggle__wrapper input[type=radio]#light:checked ~ .toggle__button {
  right: 0.1875rem;
  left: calc(100% - 1.3125rem);
}
.toggle__wrapper input[type=radio]:focus ~ .toggle__button {
  border: 0.125rem solid #fff;
}
.toggle__background {
  position: absolute;
  top: 0;
  width: 100%;
  pointer-events: none;
  display: block;
  height: 100%;
  border-radius: 0.75rem;
  background: var(--toggle-bg);
  transition: background 0.25s ease-in-out;
}
.toggle__button {
  position: absolute;
  left: 0.1875rem;
  top: 50%;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background: var(--toggle-button);
  transform: translateY(-50%);
  cursor: pointer;
  transition: all 0.25s ease-in-out;
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
  margin-bottom: 2.5rem;
}
@media (min-width: 768px) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  background: var(--card-bg);
  color: var(--text-color2);
  padding: 1.5625rem;
  border-radius: 0.3125rem;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  transition: background 0.3s ease-in-out;
}
.card:before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  height: 0.3125rem;
}
.card:hover {
  background: var(--card-hover);
}
.card--facebook:before {
  background: var(--facebook);
}
.card--twitter:before {
  background: var(--twitter);
}
.card--instagram:before {
  background: linear-gradient(225deg, var(--instagram-end) 0%, var(--instagram-middle) 50%, var(--instagram-start) 98.02%);
}
.card--youtube:before {
  background: var(--youtube);
}
.card__top {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-bottom: 1.75rem;
  height: 1.25rem;
}
.card__bottom {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-end;
}
.card__icon {
  margin-right: 0.625rem;
}
.card__username {
  font-size: 0.75rem;
  font-weight: 700;
}
.card__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
}
.card__followers {
  text-align: center;
  line-height: 1;
  margin-bottom: 1.5625rem;
  text-transform: uppercase;
}
.card__count {
  font-weight: 700;
  color: var(--text-color);
  margin-bottom: 0.625rem;
}
.card__count--big {
  font-size: 3.5rem;
}
.card__count--small {
  font-size: 2rem;
  margin: 0;
  line-height: 1;
}
.card__label {
  font-size: 0.75rem;
  color: var(--text-color2);
  letter-spacing: 0.3125rem;
}
.card__change {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  font-size: 0.75rem;
  font-weight: 700;
}
.card__change--up {
  color: var(--limegreen);
}
.card__change--down {
  color: var(--brightred);
}
.card__change img {
  margin-right: 0.3125rem;
}

.card--overview:before {
  display: none;
}
.card--overview .card__top {
  justify-content: space-between;
}

footer a {
  color: var(--text-color);
}